.flip-countdown {
  --background-color: white;
  --background-bottom-color: white;
  --text-color: rgba(85, 76, 255, 0.63);
  --text-bottom-color: rgba(85, 76, 255, 0.63);
  margin: 0;
  margin-left: auto;
}
.flip-countdown-title {
  font-size: 10px !important;
  margin-bottom: 5px;
  display: inline-block;
  color: white;
  font-weight: bold;
}
.flip-countdown.size-small .flip-countdown-piece .flip-countdown-card .flip-countdown-card-sec {
  font-weight: 700;
}