.react-tiny-popover-container {
  z-index: 9999;
}
.overlay-text-1 {
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
}
.overlay-text-2 {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-word;
}
.display-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.display-gap {
  display: flex;
  align-items: center;
  gap: 10px;
}
.display-between {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hidden {
  display: none;
}
.font-500 {
  font-weight: 500;
}
.font-700 {
  font-weight: bold;
}
.uppercase {
  text-transform: uppercase;
}
.text-color-2 {
  color: #667386;
}
.text-blue {
  color: #004dff;
}
.text-white {
  color: #fff;
}
.text-red {
  color: #ff0366;
}
.h2 {
  font-size: 28px;
}
.h4 {
  font-size: 22px;
}
.body0 {
  font-size: 17px;
  line-height: 21px;
}
.body1 {
  font-size: 15px;
  line-height: 21px;
}
.body2 {
  font-size: 13px;
  line-height: 15px;
}
.body4 {
  font-size: 11px;
  line-height: 12px;
}
.body3 {
  font-size: 9px;
  line-height: 12px;
}
.mt-1 {
  margin-top: 4px;
}
.mb-1 {
  margin-bottom: 4px;
}
.mt-2 {
  margin-top: 8px;
}
.mb-2 {
  margin-bottom: 8px;
}
.mt-5 {
  margin-top: 20px;
}
.mb-5 {
  margin-bottom: 20px;
}
.ion-black path {
  fill: #04004d;
}
.btn-disabled {
  background: #edeef0 !important;
  color: #d3d7db !important;
}
iframe {
  display: none;
}
.drag {
  opacity: 0.1;
  z-index: 0;
}
.hidden-scrollbar::-webkit-scrollbar {
  display: none;
}
.uppercase {
  text-transform: uppercase;
}
