@keyframes scaleOutCenter {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 1;
  }
  50% {
    opacity: 0.7s;
  }
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
    opacity: 0.4;
  }
}
.scaleOutCenter {
	-webkit-animation: scaleOutCenter cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
	        animation: scaleOutCenter cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
          animation-duration: 0.7s !important;
}

.text-focus-in {
	-webkit-animation: text-focus-in 1s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
	        animation: text-focus-in 1s cubic-bezier(0.550, 0.085, 0.680, 0.530) both;
}
@keyframes text-focus-in {
  0% {
    -webkit-filter: blur(12px);
            filter: blur(12px);
    opacity: 0;
  }
  100% {
    -webkit-filter: blur(0px);
            filter: blur(0px);
    opacity: 1;
  }
}

@keyframes roll-in-blurred-right {
  0% {
    -webkit-transform: translateX(2000px) rotate(720deg);
            transform: translateX(2000px) rotate(720deg);
    -webkit-filter: blur(50px);
            filter: blur(50px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translateX(0) rotate(0deg);
            transform: translateX(0) rotate(0deg);
    -webkit-filter: blur(0);
            filter: blur(0);
    opacity: 1;
  }
}
